.social {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 25px 0;
  margin: 0;
  border-bottom: 1px solid rgb(229, 229, 229);

  list-style: none;
}

.social__item {
  display: flex;
  align-items: stretch;
  margin: 0;
  margin-left: 40px;

  &:first-of-type {
    margin-left: 0;
  }
}

.social__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  text-decoration: none;
}

.social__icon {
  max-width: 25px;
  height: auto;

  fill: #231f20;
}

.social__link:hover .social__icon,
.social__link:focus .social__icon {
  fill: $primary-color-dark;
}

.social__link:active .social__icon {
  opacity: 0.3;
}

.social__name {
  @include visually-hidden;
}
